import React from 'react';
import { Link } from 'react-router-dom';

const variants = {
  primary: 'bg-[#003057] text-white hover:bg-[#004075] shadow-sm',
  secondary: 'bg-white text-[#003057] border-2 border-[#003057] hover:bg-gray-50',
  outline: 'bg-transparent text-[#003057] border border-gray-300 hover:border-[#003057]'
};

const sizes = {
  sm: 'px-4 py-2 text-sm',
  md: 'px-6 py-3 text-base',
  lg: 'px-8 py-4 text-lg'
};

const Button = ({
  variant = 'primary',
  size = 'md',
  as: Component = 'button',
  children,
  className = '',
  disabled = false,
  ...props
}) => {
  const baseStyles = 'inline-flex items-center justify-center rounded-lg font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-[#003057] focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed';
  
  const variantStyles = variants[variant];
  const sizeStyles = sizes[size];

  const combinedClassName = `${baseStyles} ${variantStyles} ${sizeStyles} ${className}`;

  return (
    <Component
      className={combinedClassName}
      disabled={disabled}
      {...props}
    >
      {children}
    </Component>
  );
};

export default Button;