import React from 'react';
import { motion } from 'framer-motion';
import { TrendingUp, TrendingDown } from 'lucide-react';

const StatCard = ({ number, label, trend }) => {
  const isTrendPositive = trend?.includes('+') || trend?.includes('Up');

  return (
    <motion.div
      whileHover={{ y: -2 }}
      className="bg-gray-50 p-6 rounded-xl border border-gray-100 hover:border-[#003057]/20 
                 hover:shadow-lg transition-all duration-200"
    >
      <div className="text-3xl font-bold text-[#003057] mb-2">{number}</div>
      <div className="text-gray-600 text-sm">{label}</div>
      {trend && (
        <div className={`flex items-center mt-2 text-sm ${
          isTrendPositive ? 'text-green-600' : 'text-red-600'
        }`}>
          {isTrendPositive ? (
            <TrendingUp className="w-4 h-4 mr-1" />
          ) : (
            <TrendingDown className="w-4 h-4 mr-1" />
          )}
          {trend}
        </div>
      )}
    </motion.div>
  );
};

export default StatCard;