import React, { useState, useEffect } from 'react';
import { Search as SearchIcon, X } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

const SearchResults = ({ results, onClose, onResultClick }) => {
  if (!results.length) {
    return (
      <div className="p-4 text-gray-500">
        No results found
      </div>
    );
  }
  
  return (
    <div className="divide-y">
      {results.map((result, index) => (
        <button
          key={index}
          onClick={() => onResultClick(result)}
          className="w-full text-left p-4 hover:bg-gray-50 transition-colors"
        >
          <div className="flex items-center justify-between">
            <div>
              <h4 className="font-medium text-gray-900">{result.title}</h4>
              <p className="text-sm text-gray-500">{result.type}</p>
            </div>
            {result.metadata && (
              <span className="text-sm text-gray-500">{result.metadata}</span>
            )}
          </div>
        </button>
      ))}
    </div>
  );
};

const SearchBar = ({ players, scheduleGames }) => {
  const navigate = useNavigate();
  const [query, setQuery] = useState('');
  const [isExpanded, setIsExpanded] = useState(false);
  const [results, setResults] = useState([]);

  const searchContent = (searchQuery) => {
    if (!searchQuery.trim()) {
      return [];
    }

    const searchResults = [];
    const lowercaseQuery = searchQuery.toLowerCase();

    // Search through players
    if (players) {
      const playerResults = players
        .filter(player => 
          player.name.toLowerCase().includes(lowercaseQuery) ||
          player.grade?.toLowerCase().includes(lowercaseQuery) ||
          player.number?.toString().includes(lowercaseQuery)
        )
        .map(player => ({
          title: player.name,
          type: 'Player',
          metadata: `#${player.number} - ${player.grade}`,
          link: `/players/${player.name.toLowerCase().replace(/\s+/g, '-')}`,
          playerData: player
        }));
      searchResults.push(...playerResults);
    }

    // Search through schedule
    if (scheduleGames) {
      const gameResults = scheduleGames
        .filter(game => 
          game.opponent.toLowerCase().includes(lowercaseQuery) ||
          game.date.toLowerCase().includes(lowercaseQuery) ||
          game.type.toLowerCase().includes(lowercaseQuery)
        )
        .map(game => ({
          title: `${game.opponent} ${game.location === 'Home' ? 'vs' : '@'} ESD`,
          type: 'Game',
          metadata: game.date,
          link: '/schedule'
        }));
      searchResults.push(...gameResults);
    }

    return searchResults.slice(0, 3); // Limit to top 5 results
  };

  useEffect(() => {
    const results = searchContent(query);
    setResults(results);
  }, [query]);

  const handleClose = () => {
    setQuery('');
    setIsExpanded(false);
  };

  const handleResultClick = (result) => {
    navigate(result.link, { 
      state: result.playerData ? { playerData: result.playerData } : undefined 
    });
    handleClose();
  };

  // Handle click outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (isExpanded && !event.target.closest('.search-container')) {
        setIsExpanded(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isExpanded]);

  return (
    <div className="relative w-full search-container z-50">
      <div className={`relative transition-all duration-300 ${
        isExpanded ? 'ring-2 ring-[#003057]' : ''
      }`}>
        <input
          type="text"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          onFocus={() => setIsExpanded(true)}
          placeholder="Search players, games, and more..."
          className="w-full pl-10 pr-4 py-3 rounded-lg border focus:outline-none focus:border-[#003057] text-gray-900"
        />
        <SearchIcon className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-5 h-5" />
        {query && (
          <button
            onClick={handleClose}
            className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
          >
            <X className="w-5 h-5" />
          </button>
        )}
      </div>
      {isExpanded && query && (
        <div className="absolute top-full left-0 right-0 mt-2 bg-white rounded-lg shadow-lg border max-h-96 overflow-y-auto z-[100]">
          <SearchResults 
            results={results}
            onClose={handleClose}
            onResultClick={handleResultClick}
          />
        </div>
      )}
    </div>
  );
};

export default SearchBar;